import Vue from 'vue'
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
      LoggedIn: false,
      IsManager: false,
    },
    mutations: {
      login (state) {
        state.LoggedIn = true;
      },
      logout (state) {
        state.LoggedIn = false;
      }
    }
})

export default store;